body{
  width: 400px;
  margin: 0 auto;
  text-align: center;
}

.notification {
    color: #19a919;
    background: #c6f9c6;
    border: 1px solid #19a919;
    border-radius: 5px;
    padding: 7px;
    margin-bottom: 10px;
  }
  
  .error {
    color: #f50000;
    background: #ffdddd;
    border: 1px solid #f50000;
    border-radius: 5px;
    padding: 7px;
    margin-bottom: 10px;
  }
  